// RANGE SLIDER (nouislider)

.slider_scope {
	padding-right: 4px;
}

.global_form .slider_scope {
	margin: 26px 14px 6px 14px;
}

.noUi-target {
	height: 8px;
	border: 0;
	border-radius: var(--b_radius);
	box-shadow: none;
	background-color: #ddd;
}

.noUi-horizontal .noUi-tooltip {
	color: var(--primary);
	font-weight: 600;
	font-size: 14px;
	border: 0;
	background-color: transparent;
	bottom: 18px;
}

.noUi-horizontal .noUi-handle {
	cursor: pointer;
	height: 24px;
	width: 24px;
	right: -12px;
	top: -8px;
	border-radius: 50%;
	background: var(--primary);
	border: 2px solid #f7f7f7;
	box-shadow: none;
	transition: all 0.2s;

	&:hover {
		background: var(--primary_form_light);
		transition: all 0.2s;
	}
}

.noUi-handle::before,
.noUi-handle::after {
	display: none;
}
