
	/* it cuts off spaces and make it too clustered */
	.bold_move_to_side b,
	.euro_move_to_side {
		padding-left: 3px;
	}

	.the_form_is_disabled {
		pointer-events: none;
		opacity: 0.7;
	}

	.price_has_details {
		cursor: pointer;
		.amount {
			transition: color 0.2s;
			color: var(--warning);
		}
		&:hover {
			.amount {
				color: var(--text);
			}
		}
	}
