// DOCS LANDING PAGE STYLES

/*
	dl_item_ = sedy blok
	dl_item_text = textovy blok
	dl_item_file = blok pre file upload (multi + single)
	dl_item_action = blok pre odoslanie do poistovne (multi)
*/

.docs_landing_scope {

	// change from rem to px (lukas)
	h1, .h1 { font-size: 40px; }
	h2, .h2 { font-size: 32px; }
	h3, .h3 { font-size: 28px; }
	h4, .h4 { font-size: 24px; }
	h5, .h5 { font-size: 20px; }
	h6, .h6 { font-size: 16px; }

	.dl_item_text,
	.dl_item_file,
	.dl_item_action {
		background-color: #f7f7f7;
		margin-bottom: 30px;
		border-radius: calc(var(--b_radius) * 2);
	}

	.dl_item_file.grouped_not_last {
		margin-bottom: 4px;
	}

	.dl_item_text,
	.dl_item_action {
		padding: 20px
	}

	div p:last-child {
		margin-bottom: 0;
	}


	// BASIC LAYOUT / NO GRID / SINGLE

	.dl_filename,
	.dl_up_download,
	.dl_status,
	.dl_action {
		display: flex;
	}

	.dl_status,
	.dl_action {
		background-color: #eee;
	}

	.dl_status:last-of-type,
	.dl_action {
		border-top-right-radius: calc(var(--b_radius) * 2);
		border-bottom-right-radius: calc(var(--b_radius) * 2);

		@include media-breakpoint-down(md) {
			border-top-right-radius: 0;
			border-bottom-right-radius: calc(var(--b_radius) * 2);
			border-bottom-left-radius: calc(var(--b_radius) * 2);
		}
	}

	.dl_title {
		padding: 0 8px 8px; // p-2 (.5rem)

		h5 {
			color: var(--text);
			font-weight: 600;
			font-size: 14px;
			margin-bottom: 0;
		}
	}

	.dl_image {
		.img_wrapper {
			border: 1px solid #ddd;
			border-radius: var(--b_radius);
			padding: 2px;
			background-color: #fff;

			// for preview image
			height: 73px;
			overflow: hidden;
			display: flex;
			align-items: center; /* vertical */
			justify-content: center; /* horizontal */
		}

		img {
			max-width: 100%;
			height: auto;
			opacity: .2;
		}
	}

	.dl_filename {
		overflow: hidden;
		text-overflow: ellipsis;
		padding: 20px 0;
		width: 180px;

		@include media-breakpoint-down(md) {
			width: initial;
		}
	}

	.dl_up_download {
		white-space: nowrap;

		input[type="file"] {
			display: none;
		}

		button:last-child {
			margin-left: 8px
		}
	}

	.dl_progress {
		flex-basis: 8px;
		background-color: #ddd;
		position: relative;
	}

	.dl_bar {
		position: absolute;
		bottom: 0;
		left: 0;
		background-color: var(--primary);

		@include media-breakpoint-down(md) {
			height: 8px;
		}

		&.bar_vertical {
			width: 8px;
		}

		&.bar_horizontal {
			height: 8px;
		}
	}

	.dl_status {
		flex-grow: 1;
		font-weight: 600;

		p {
			display: inline-block;
		}
	}


	// STAVY

	.dl_success,
	.dl_sent {
		.dl_bar {
			background-color: var(--primary);
		}

		.dl_status {
			color: var(--primary);
		}

		.dl_image img {
			opacity: 1;
		}
	}

	.dl_success .dl_status p {
		background-repeat: no-repeat;
		padding-left: 25px; // 20 icon width + 10 space
	}

	.dl_sent .dl_status p {
		background-repeat: no-repeat;
		padding-left: 37px; // 27 icon width + 10 space
	}

	.dl_warning {
		.dl_bar {
			background-color: var(--warning);
		}

		.dl_status {
			color: var(--warning);

			p {
				background-repeat: no-repeat;
				padding-left: 30px; // 20 icon width + 10 space

			}
		}
	}

	.btn-download.v_hidden {
		visibility: hidden;
		@include media-breakpoint-down(md) {
			display: none;
		}
	}

	// GRID LAYOUT - TILES

	.tiles {
		.dl_title {
			padding-top: 24px; // p-3 (1.5rem)
		}

		.img_wrapper {
			background: linear-gradient(0deg, rgba(235, 235, 235, 1) 0%, rgba(255, 255, 255, 1) 100%);
			height: 247.5px;
			@include media-breakpoint-down(md) {
				height: 180px;
			}
		}

		.dl_image img {
			opacity: 1;
		}

		.btn-download.v_hidden {
			display: none;
		}

		.dl_bar {
			height: 8px;
		}

		.dl_status {
			border-bottom-left-radius: calc(var(--b_radius) * 2);
			border-bottom-right-radius: calc(var(--b_radius) * 2);
		}
	}


	// EXTRA

	.dl_add, .dl_remove {
		display: inline-block;
		width: 20px;
		height: 20px;
		cursor: pointer;
	}

	.cross_on_hover {
		position: relative;
		display: inline-block;
		margin-right: 4px;
		margin-left: 4px;

		.overlay {
			cursor: pointer;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
			opacity: 0;
			transition: .5s ease;
			background-color: var(--secondary); // if neutral: #4d4d4d
		}

		.delete_symbol {
			background-image: url("@/assets/images/common/ic_clear_np.svg");
			width: 14px;
			height: 14px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			text-align: center;
		}

		&:hover .overlay {
			opacity: 0.8;
		}
	}

}


// SKINS

@each $skin-slug in $skin-list {
	&.#{$skin-slug} {

		.dl_success .dl_status p {
			background-image: url("@/assets/images/#{$skin-slug}/results/ic_check_np.svg");
		}

		.dl_sent .dl_status p {
			background-image: url("@/assets/images/#{$skin-slug}/results/ic_check_double_np.svg");
		}

		.dl_warning .dl_status p {
			background-image: url("@/assets/images/#{$skin-slug}/results/ic_warning_np.svg");
		}

		.dl_add {
			background-image: url("@/assets/images/#{$skin-slug}/results/ic_add_circle_outline_np.svg");
		}

		.dl_remove {
			background-image: url("@/assets/images/#{$skin-slug}/results/ic_remove_circle_outline_np.svg");
		}
	}
}