// DATEPICKER

//input
.input-datepicker {
	color: var(--primary);
	text-align: center;
	padding-right: 42px; // 36px icon + 8px input area padding
}


// calendar
.datepicker {

	position: absolute;
	top: 54px;
	@include media-breakpoint-down(md) {
		top: 48px;
	}
	@include media-breakpoint-down(sm) {
		top: 76px;
	}
	&.aligned_to_top {
		top: -315px;
		@include media-breakpoint-down(md) {
			top: -320px;
		}
		@include media-breakpoint-down(sm) {
			top: -294px;
		}
	}

	.calendar {
		position: absolute;
		z-index: 100;
		background: #fff;
		width: 300px;
		border: 1px solid #ccc;
		border-radius: var(--b_radius);

		header,
		.week_days,
		footer {
			font-weight: 600;
		}

		header {
			display: block;
			line-height: 40px;

			span {
				display: inline-block;
				text-align: center;
				width: 58%;
				float: left;
				cursor: pointer;
				&:hover {
					background: var(--primary_bg);
				}
			}

			.prev, .next {
				width: 21%;
				float: left;
				position: relative;
				font-size: 18px;

				&:after {
					content: '';
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translateX(-50%) translateY(-50%);
					border: 6px solid transparent;
				}
			}
		}

		.cell {
			display: inline-block;
			padding: 0 5px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			vertical-align: middle;
			border: 1px solid transparent;
			border-radius: var(--b_radius);

			width: 14.285714285714286%;
			&.month {
				width: 33.333%;
			}
			&.year {
				width: 20%;
			}

			&.week_days {
				white-space: nowrap;
				cursor: inherit;

				&:hover {
					background: inherit;
				}
			}

			&.today:not(.picked) {
				border: 1px dotted var(--primary);
				position: relative;

				&::after {
					content: '';
					display: inline-block;
					border: solid transparent;
					border-width: 0 0 7px 7px;
					border-bottom-color: var(--primary);
					border-top-color: rgba(0, 0, 0, 0.2);
					position: absolute;
					bottom: 2px;
					right: 2px;
				}
			}

			&.picked {
				cursor: pointer;
				background-color: var(--primary);
				color: #fff;
			}

			&.date_disabled {
				opacity: 0.3;
				cursor: not-allowed;
			}

			&.other_month {
				opacity: 0.3;
			}

			&:not(.blank):not(.date_disabled):not(.picked):not(.week_days) {
				cursor: pointer;

				&:hover {
					background: var(--primary_bg);
				}
			}
		}
	}
}

// in global_form, datepicker was strangely aligned away from input
.global_form .datepicker {
	top: initial;
}

// SKINS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {
		.input-datepicker {
			background: #fff url("@/assets/images/#{$skin-slug}/forms/field-calendar.svg") right center no-repeat;
		}
	}
}
