
	// fonts, got to be outside of deep scope
	@import '@/assets/styles/fonts.scss';

	// all app scoped styles
	.osm_app_scope::v-deep() {
		@import '@/style_imports.scss';
	}

	// loading is on same level as scope, so lets keep it outside
	.loading > .page_wrap {
		opacity: 0.6;
		pointer-events: none;
	}

	.gray_background {
		background: #F5F5F5;
	}
