// VALIDATION

.validation-placeholder {
	min-height: 14px;
	font-size: 13px;
	line-height: 1;
	margin-top: 5px;
}

.validation-message {
	color: var(--warning);

	> span {
		margin-right: 4px;

	}
}

.input-block.validation-error {
	border-color: var(--warning);
	background-color: var(--warning_bg);
}

.mark-circle {
	display: inline-block;
	border-radius: 50%;
	width: 14px;
	height: 14px;
	text-align: center;
	line-height: 14px;
}

span.mark-circle:not(:first-of-type) {
	margin-left: 7px;
}

.mark-orange {
	background-color: var(--warning);
	color: #fff;
	font-weight: 600;
}

.mark-orange-inv {
	background-color: transparent;
	color: var(--warning);
	font-weight: 600;
	border: 1px solid var(--warning);
}
