// SELECT + SEARCHABLE SELECT

select,
.input-autocomplete input.form-control {
	overflow: hidden;
	// background-image: skin-specific
	background-color: #fff;
	background-repeat: no-repeat;
	background-position: right center;
	cursor: pointer;
}

select.form-control,
.input-autocomplete input.form-control {
	padding-right: 40px; // 36px icon + 4px space
}

select.form-control[multiple] {
	padding-right: 12px;
	background-image: none;
}


// SKINS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		select {
			background-image: url("@/assets/images/#{$skin-slug}/forms/field-select.svg");
		}

		.input-autocomplete input.form-control {
			background-image: url("@/assets/images/#{$skin-slug}/forms/field-autocomplete.svg");
		}
	}
}
