// SKIN OVERRIDES (style overrides, skin-specific elements & visuals)


&.skin-najpoistenie {
	$skin-slug : "skin-najpoistenie";

	// step2

	.pr_bundle {
		margin-left: 50px;
		background: var(--primary);
		color: #fff;
		border-radius: 4px;

		&::after {
			content: "";
			position: absolute;
			top: 50%;
			left: -5px;
			transform: translateY(-50%);
			border-style: solid;
			border-width: 5px 5px 5px 0;
			border-color: transparent var(--primary);
			display: block;
			width: 0;
			z-index: 1;
		}

		span.bundle_img {
			background: url("@/assets/images/#{$skin-slug}/results/m-bundle.png") center center no-repeat;
			display: block;
			position: absolute;
			bottom: -5px;
			left: -60px;
			width: 53px;
			height: 44px;
		}
	}

	// steps

	span.i_next_step1 {
		background: url("@/assets/images/#{$skin-slug}/special/next-steps-1.png") center center no-repeat;

		&:before {
			content: ""
		}
	}

	span.i_next_step2 {
		background: url("@/assets/images/#{$skin-slug}/special/next-steps-2.png") center center no-repeat;

		&:before {
			content: ""
		}
	}

	span.i_next_step3 {
		background: url("@/assets/images/#{$skin-slug}/special/next-steps-3.png") center center no-repeat;

		&:before {
			content: ""
		}
	}
}


&.skin-finportal {

	$skin-slug : "skin-finportal";

}


&.skin-partners {

	$skin-slug : "skin-partners";

}


&.skin-zeteo {

	$skin-slug : "skin-zeteo";

	// this color is also hooked on .osm_app_scope.gray_background one level above skin class
	background: #F5F5F5;

	// white bg & drop shadow
	.results_action_area .form_summary,
	.results_action_area .global_form,
	#filters_form,
	.input_tab,
	.filling,
	.step4 .docs_download,
	.prop_confirm {
		background-color: #FFF;
		box-shadow: 0px 0px 3px 0px rgba(148, 157, 168, 0.25);
	}

	.results_action_area .form_summary .data_edit,
	#filters_form .filter_head {
		background: #FFF;
	}

	// form
	.input-block.input-block--alt {
		min-height: initial;

		.input-label {
			font-weight: normal;
			color: #666;
		}

		.ttip {
			border-radius: 50%;
			border: 1px solid #b2b2b2;
			width: 16px;
			height: 16px;
			text-align: center;
			color: #b2b2b2;
			font-size: 10px;
			//line-height: 14px;

			&:hover {
				border-color: var(--primary);
				color: var(--primary);
			}
		}

		.ttip .ttip_text {
			bottom: calc(100% + 10px);
		}
	}

	.input-button-icon input[type="checkbox"] + label {
		border-radius: 4px;
	}

	.form-control {
		border: 1px solid #e0e0e0;
		border-radius: 4px;

		&:focus {
			border-color: var(--primary);
		}
	}


	// step2 & product
	.input_tab {
		padding-top: 16px;
		padding-bottom: 16px;
	}

	.product {
		box-shadow: 0px 6px 16px 0px rgba(148, 157, 168, 0.25);
	}

	.pr_area_info {
		border-color: #FFF;
	}

	.pr_area_price {
		border-color: #FFF;
		border-left: 1px solid #F5F5F5;
	}

	.results_deliver {
		text-align: right;
	}

}
