// TITLE AREA (STEP2, STEP4 etc.)

.calc_title_area {
	text-align: center;
	margin-bottom: 48px;

	p {
		margin-bottom: 0;

		@include media-breakpoint-up(md) {
			padding: 0 24px;
		}

		@include media-breakpoint-up(lg) {
			padding: 0 48px;
		}
	}

	h2:not(:first-of-type) {
		margin-top: 48px;
	}
}


// INFO

.info_for_session {

	h2, p {
		display: inline-block;
	}

	h2 {
		font-size: 28px;
		margin-bottom: 0px;
	}

	p {
		font-size: 12px;
		margin-left: 1.5rem;
		margin-right: 1.5rem;
		@include media-breakpoint-down(sm) {
			display: none;
		}
	}

}
