// SEARCH

.input-search {
	border-right-width: 0;
	text-transform: uppercase;
	&::placeholder {
		text-transform: initial;
	}
}

.search_input_error {
	margin-left: 15px;
	display: flex;
	justify-content: center;
	align-content: center;
	flex-direction: column;
}

.input-search-icon {
	display: block;
	height: 36px;
	width:36px;
	// background: skin-specific
	cursor: pointer;
	border-top-right-radius: var(--b_radius);
	border-bottom-right-radius: var(--b_radius);
}


// SKINS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		.input-search-icon {
			background: url("@/assets/images/#{$skin-slug}/forms/field-search.svg") right center no-repeat;
		}
	}
}
