// SKIN VARIABLES

$skin-list: skin-najpoistenie, skin-finportal, skin-myport, skin-partners, skin-zeteo;

&.skin-najpoistenie {

	/*

	WEB
	blue: #268fa3; RGB 38, 143, 163; rgba(38,143,163,0.85);
	blue hover (85%): #469FB0;
	blue bg: #eef9fb;
	blue bg border: #c9ebf2;
	darkblue: #1a4c6e; RGB 26, 76, 110; rgba(26,76,110,0.85);
	orange: #f28f38; RGB 242, 143, 56; rgba(242,143,56,0.85);
	orange bg: #fdf3eb;

	*/

	// Colors

	--text: #4d4d4d; /* space grey (matterhorn) */
	/* default font color */

	--primary: #268fa3; /* blue */
	/* default primary color - links, buttons, some headings, nav items, horizontal lines... */

	--primary_light: rgba(38,143,163,0.85); /* np blue .85 */
	/* lighter primary color - hover for blue bg elements (e.g. buttons) */

	--primary_bg: #eef9fb; /* blue bg */
	/* very light background color - white button hover etc. */

	--pr_bg: #eef9fb;
	/* step2 product background color */

	--pr_cta: #f28f38; /* orange */
	/* step2 product cta */

	--pr_cta_light: rgba(242, 143, 56, 0.85);
	/* step2 product cta hover */

	--primary_bgdark: #c9ebf2; /* blue bg border */
	/* borders & lines for light backgrounds (products on results page) */

	--secondary: #1a4c6e; /* dark blue */
	/* link hovers, headings for content pages, etc. */

	--accent:  #f28f38; /* orange */
	/* default accent color - orange elements (e.g. buttons) */

	--accent_alt:  #f28f38; /* orange */
	/* alternative accent color - pr_rating stars */

	--accent_light: rgba(242,143,56,0.85); /* orange .85 */
	/* lighter accent color - hover for orange bg elements (e.g. buttons) */

	--accent_bg: #fdf3eb; /* orange bg */
	/* just in case, not being used at this time */

	--warning: #f28f38; /* orange */
	/* warnings, validation, error messages */

	// --warning_light: rgba(242,143,56,0.85); /* orange .85 */
	/* hover for warnings, validation, error messages */

	--warning_bg: #fdf3eb; /* orange bg */;
	/* background for error messages */

	//--light: #f7f7f7; /* lynx white */
	/* form fields input blocks bg... */

	--primary_form: #268fa3; /* blue */
	/* form fields primary color */

	--primary_form_light: #469FB0; /* blue hover */
	/* form fields light primary color - form element hovers */

	// Colors END


	// Design

	--b_radius: 0;

	// Design END

}


&.skin-finportal {

	/* FINPORTAL COLOR SCHEME

	spruce blue: #004a5d; RGB 00, 74, 93; rgba(0,74,93,0.85);
	spruce blue hover (85%): #266575;
	spruce blue bg (10%): #E5ECEE;
	mint (turquoise mint): #6fc7b6; RGB 111, 199, 182; rgba(111,199,182,0.85);
	mint hover (85%): #84CFC0;
	mint bg (10%): #F0F9F8;
	mint bg border: #d2ede8; (mint tint color variation -4)
	red: #DB5252; RGB 219, 82, 82 rgba(219,82,82,0.85);
	red bg: #fceded;
	fp orange (by bluekiwi): #ffb643; rgba(255,182,67,0.85);

	colors for light bg page/calc (#F6F8F9 - spruce blue 3% opacity)
	input field bg:	#EAF0F1 (spruce blue 8% opacity)

	*/

	// Colors

	--text: #4d4d4d; /* space grey (matterhorn) */
	/* default font color */

	// --primary: #004a5d; /* spruce blue */
	--primary: #004a5d; /* mint */
	/* default primary color - links, buttons, some headings, nav items, horizontal lines... */

	// --primary_light: rgba(219, 82, 82, 0.85); /* spruce blue .85 */ (DOUBLE CHECK!)
	--primary_light: rgba(0,74,93,0.85); /* spruce blue .85 */
	/* lighter primary color - hover for blue bg elements (e.g. buttons) */

	--primary_bg: #F0F9F8; /* mint bg */
	/* very light background color - white button hover etc. */

	--pr_bg: #F0F9F8; /* mint bg */
	/* step2 product background color */

	--pr_cta: #6fc7b6; /* mint */
	/* step2 product cta */

	--pr_cta_light: rgba(111, 199, 182, 0.85);
	/* step2 product cta hover */

	--primary_bgdark: #d2ede8; /* mint bg border */
	/* borders & lines for light backgrounds (products on results page) */

	--secondary: #004a5d; /* spruce blue */
	/* link hovers, headings for content pages, etc. */

	--accent:  #ffb643; /* fp orange */
	/* default accent color - orange elements (e.g. buttons) */

	--accent_alt:  #ffb643; /* fp orange */
	/* alternative accent color - pr_rating stars */

	--accent_light: rgba(255,182,67,0.85); /* fp orange .85 */
	/* lighter accent color - hover for orange bg elements (e.g. buttons) */

	--accent_bg: #fceded; /* red bg */
	/* just in case, not being used at this time */

	--warning: #DB5252; /* finportal reality (red) */
	/* warnings, validation, error messages */

	// --warning_light: rgba(219,82,82, 0.85); /* red .85 */
	/* hover for warnings, validation, error messages */

	--warning_bg: #fceded; /* red bg */
	/* background for error messages */

	//--light: #f7f7f7; /* lightest grey  (lynx white) */
	/* form fields input blocks bg... */

	--primary_form: #6fc7b6; /* mint */
	/* form fields primary color */

	--primary_form_light: #84CFC0; /* mint hover */
	/* form fields light primary color - form element hovers */

	// Colors END


	// Design

	--b_radius: 4px;

	// Design END

}

