&.skin-zeteo {

	/*

	zeteo blue: #2A77D1;	RGB 42, 119, 209; rgba(42, 119, 209, 0.85);
	zeteo blue hover (85%): #498BD7;
	zeteo blue bg: #FAFCFE;
	zeteo blue bg border: #BED6F1
	zeteo orange accent: #F37021; RGB 243, 112, 33; rgba(243, 112, 33, 0.85);
	zeteo red (warning): #CC004C;
	zeteo red (warning) bg: #FEFAFB;
	zeteo darker blue: #1d5392; (shade +3 from color-hex.com)



	*/

	// Colors

	--text: #333; /* zeteo app text color */
	/* default font color */

	--primary: #2A77D1; /* zeteo blue */
	/* default primary color - links, buttons, some headings, nav items, horizontal lines... */

	--primary_light: rgba(42, 119, 209, 0.85); /* zeteo blue .85 */
	/* lighter primary color - hover for blue bg elements (e.g. buttons) */

	--primary_bg: #FAFCFE; /* zeteo blue bg */
	/* very light background color - white button hover etc. */

	--pr_bg: #FFF;
	/* step2 product background color */

	--pr_cta: #2A77D1; /* zeteo blue */
	/* step2 product cta */

	--pr_cta_light: rgba(42, 119, 209, 0.85);
	/* step2 product cta hover */

	--primary_bgdark: #F5F5F5; /* neutral bg border */
	/* borders & lines for light backgrounds (products on results page) */

	--secondary: #1d5392; /* darker blue */
	/* link hovers, headings for content pages, etc. */

	--accent: #F37021; /* zeteo accent */
	/* default accent color - orange elements (e.g. buttons) */

	--accent_alt: #F37021;	/* zeteo accent */
	/* alternative accent color - pr_rating stars */

	--accent_light: rgba(243, 112, 33, 0.85); /* zeteo accent .85 */
	/* lighter accent color - hover for orange bg elements (e.g. buttons) */

	--accent_bg: #FFF4E9;
	/* red bg */
