	// --warning_light: rgba(248, 148, 44, 0.85);
	/* yellow orange .85 */
	/* hover for warnings, validation, error messages */

	--warning_bg: #FAE5ED;
	/* yellow orange bg */
	/* background for error messages */

	//--light: #f7f7f7;
	/* lightest grey  (lynx white) */
	/* form fields input blocks bg... */

	--primary_form: #2A77D1;
	/* zeteo blue */
	/* form fields primary color */

	--primary_form_light: #498BD7;
	/* zeteo blue hover */
	/* form fields light primary color - form element hovers */

	// Colors END


	// Design

	--b_radius: 2px;

	// Design END

}
