#filters_form {

	background-color: #f7f7f7;
	border-radius: var(--b_radius);
	margin-top: 3px;

	.filter_toggle {
		margin-bottom: 0px;
		color: var(--primary);
		cursor: pointer;
		pointer-events: auto;
		transition: all .2s;
	}

	.filter_head {
		background-color: #eee;
		padding: 4px;
		pointer-events: none;
		transition: all .2s;
		border-radius: var(--b_radius);

		&:hover {
			background-color: #e7e7e7;
			transition: all .2s;
		}

		.i_adjust {
			display: inline-block;
			background: var(--primary);
			-webkit-mask-image: url("@/assets/images/results/adjust.svg");
			mask-image: url("@/assets/images/results/adjust.svg");
			width: 13.542px;
			height: 13px;
			margin-right: 4px;
		}
	}

	.product_selection {
		padding: 8px;
		border-top: 1px solid #fff;

		.form-check:hover {
			opacity: .85;
		}
	}

}
