// BUTTONS

.btn,
button {
	padding: 8px 12px;
	border-radius: var(--b_radius);
	transition: all 0.2s;
	text-transform: uppercase;

	&:focus {
		outline: 0;
		box-shadow: none;
	}

	&:active {
		box-shadow: none;
	}
}

// btn sizes
.btn-xlg {
	padding: 14px 30px;
	font-size: 16px;
	line-height: 1.3333333;
	text-transform: uppercase;
	letter-spacing: 1px;
}

.btn-lg {
	font-size: 16px;
}

.btn-m {
	font-size: 14px;
	padding: 5px 12px; // btn height 33px for 14px font size
}

/* clear_session_modal.php, back-modal.php,...? */
.btn-w80 {
	min-width: 80px;
}

// btn colors
.btn-primary,
.btn-blue {
	color: #fff;
	background-color: var(--primary);
	border-color: var(--primary);

	&:hover,
	&:active,
	&:focus,
	&:not(:disabled):not(.disabled):active {
		color: #fff;
		background-color: var(--primary_light);
		border-color: var(--primary_light);
	}

	&:disabled {
		background-color: var(--primary);
		border-color: var(--primary);
	}
}

.btn-outline-primary {
	border-color: var(--primary);
	color: var(--primary);

		&:hover,
		&:active,
		&:focus {
			background-color: var(--primary_bg);
			border-color: var(--primary);
			color: var(--primary);
		}
}

.btn-inactive,
.btn-inactive:hover,
.btn-inactive:active,
.btn-inactive:focus {
	background-color: #f5f5f5;
	border: 1px solid #ccc;
	cursor: default;
}

.btn-blue:focus {
	color: #fff;
}

.btn-orange,
.btn-accent {
	background-color: var(--accent);
	color: #fff;

	&:hover {
		background-color: var(--accent_light);
		color: #fff;
	}
}

.btn-grey,
.btn-light {
	background-color: #eee;
	color: var(--primary);

	&:hover,
	&:focus {
		background-color: #f7f7f7;
		color: var(--primary);
		border-color: transparent;
	}
}

.btn-white {
	color: var(--primary);
	background: #fff;
}

.btn-white:hover {
	color: var(--primary);
	background-color: var(--primary_bg);
}

.form-pagination .btn-white,
#content_subpage .btn-white {
	border: 1px solid #ddd;
}

// btn types
.btn-text { // <a href> behavior for <button>
	padding: 0;
	background-color: transparent;
	text-transform: initial;
	border-width: 0;
	color: var(--primary);
	transition: all 0.2;
	word-break: break-all;

	&:hover,
	&:focus {
		color: var(--secondary)
	}

	&.i_anchor {
		white-space: nowrap;

		&:hover {
			// anchors w/ icons
			color: var(--primary);
			opacity: .85;
		}
	}
}

.i_anchor .btn-text:hover,
.i_anchor .btn-text:focus {
	color: var(--primary);
}

.btn-left {
	background-image: url("@/assets/images/common/caret-left-white.svg");
	background-repeat: no-repeat;
	background-position: 10px center;
	padding-left: 30px;
	text-align: left;
}

.btn-send {
	background-image: url("@/assets/images/common/ic_send_np.svg");
	background-repeat: no-repeat;
	background-position: right 10px center;
	padding-right: 43px; // 12 padding + 21 icon + 10 space
}

.btn-download {
	background-image: url("@/assets/images/common/ic_file_download_np.svg");
}

.btn-upload {
	background-image: url("@/assets/images/common/ic_file_upload_np.svg");
}

.btn-download,
.btn-upload {
	background-repeat: no-repeat;
	background-position: left 10px center;
	padding-left: 36px; // 12 padding + 14 icon + 10 space

	&.btn-compact {
		width: 38px;
		height: 33px;
		padding: 0;
	}
}

.form-pagination .btn {
	min-width: 148px;
	margin: 4px;
	text-align: center;
}


// SKINS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		.btn-left.btn-light,
		.btn-left.btn-white,
		.btn-left.btn-outline-primary {
			background-image:url("@/assets/images/#{$skin-slug}/general/caret-left.svg");
		}

		.btn-right {
			background-image: url("@/assets/images/#{$skin-slug}/general/caret-right-white.svg");
			background-repeat: no-repeat;
			background-position: right 10px center;
			padding-right: 30px;
		}
	}
}