// MYPORT BS3

// tabs (bs4 html from osm)
li {
	width: initial;
}

@media (min-width: 768px) {
	.nav-justified > li,
	.nav-tabs.nav-justified > li {
		width: initial;
	}
}

// modal product detail header justify content
.modal-header::before,
.modal-header::after {
	content: none;
}

// modal product detail tables
tbody,
td,
tfoot,
th,
thead,
tr,
.table-bordered > thead > tr > th,
.table-bordered > thead > tr > td,
.table-bordered > tbody > tr > th,
.table-bordered > tbody > tr > td,
.table-bordered > tfoot > tr > th,
.table-bordered > tfoot > tr > td {
	border: 1px solid #ddd
}

input:checked + label, // checkbox checked
.table td { // modal product detail
	color: var(--text)
}

// step2 filter button
.btn,
button {
	font-size: 16px;
}

// tooltipy v myporte pretrcali
.btn {
	white-space: initial;
}

// .spinner nam prepisuju
.spinner {
	visibility: initial;
	height: initial;
	width: initial;
}

// .datepicker classu tiez pouzivaju
.datepicker {
	padding: initial;
	border-radius: initial;
	direction: initial;
	.calendar {
		display: initial;
	}
}

// NP WORDPRESS
.ttip {
	margin-top: initial;
	background: initial;
}

// NP wp - Scan OP
.drag-n-drop {
	padding: initial;
}

// FP novy web, sipka z buttonu Pokracovat
.btn {
	background-size: initial;
}

// FP web, globalne odsadenie sekcii
section {
	padding-top: initial;
	padding-bottom: initial;
}
