// INPUT BLOCK
.input-block {
	background-color: #f7f7f7;
	padding: 8px 25px 8px 5px; // pr 20px ttip + 5px horizontal padding
	margin: 5px 0 5px;
	border: 1px solid #ddd;
	border-radius: var(--b_radius);
	position: relative;
	min-height: initial;

	@include media-breakpoint-up(md) {
		min-height: 70px; // same height for 1- and 2-line labels
	}

	p {
		margin-bottom: 0;
	}
}

.input-label {
	font-weight: 600;
	width: 100%;
	padding: 3.5px;

	@include media-breakpoint-up(sm) {
		flex: 0 0 150px;
	}
}

.input-area {
	width: 100%;
	padding: 3.5px;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.input-block-blank {
	background-color: transparent;
	border-color: transparent;
}

.input-block-large {
	min-height: initial;

	@include media-breakpoint-up(md) {
		min-height: 92px;
	}
}

.input-required {
	background-position: 3px 3px;
	background-repeat: no-repeat;

	@include media-breakpoint-up(sm) {
		background-position: 7px 7px;
	}
}

.input-label-full {
	flex-direction: column !important;

	.input-label {
		flex-basis: auto;
	}

	&.input-required {
		background-position: 3px 3px;
	}
}

// INPUT BLOCK W/LABEL ABOVE (ZETEO STYLE)

.input-block.input-block--alt {
	background: transparent;
	padding: 0;
	border: 0;

	.input-label {
		display: inline-block;
		width: auto;
		vertical-align: middle;
	}

	.ttip {

	}
}
