// COMMON ELEMENTS

// light background inside bs columns
.filling,
.zmluva { // "view" html templates (prop_wording)
	padding: 20px;
	background-color: #f7f7f7;
	height: 100%;
	border-radius: calc(var(--b_radius) * 2);
}


// STEP4 (PROPOSAL)

.prop_wording,
.step4 .docs_download {
	margin-bottom: 24px;
}

.step4 .docs_download,
.prop_confirm {
	background: #f7f7f7;
	padding: 20px;
	border-radius: calc(var(--b_radius) * 2);

	h3 {
		color: var(--primary)
	}
}

// proposal wording
.prop_wording {

	.col-12:first-of-type h3 {
		margin-top: 24px;
	}

	h2 {
		text-align: center;
	}

	p {
		text-align: justify;
	}

	h3 {
		color: var(--primary);
	}

	ul {
		list-style-type: none;
	}

	li,
	ol {
		margin-bottom: 8px;
		overflow-wrap: break-word;
	}
}


// proposal confirm form and navigation
.prop_confirm {

	h3 {
		margin-bottom: 24px;
	}

	.confirm_check .form-check {
		margin-bottom: 8px;
	}

	.form-pagination {
		margin-top: 24px;
	}
}

.proposal .documents .col,
.proposal .confirmation .col { //toto zatial len v demo template - dohodnut s LM, ako pouzit v Step4_pr...vue
	margin-top: 12px;
	margin-bottom: 12px;
}

.zmluva {
	height: 100%;
	background: #F7F7F7;
	padding: 20px;

	h3 {
		margin-top: 5px;
	}

	ul,
	ol {
		margin-top: 10px;
		overflow-wrap: break-word;
	}

	ul li b {
		color: #000000;
		padding-left: 10px;
	}

	ol li {
		margin-left: 20px;
		margin-bottom: 16px;
	}

	ul li {
		margin-bottom: 8px;
	}
}


// STEPS 5 - 8 (Done, Payment Success/Fail)

// point boxes
.point-box {
	background: #f7f7f7;
	padding: 0;
	border-radius: calc(var(--b_radius) * 2);
	// margin-top: 10px;
	// margin-bottom: 10px;

	h3,
	h4 {
		color: var(--primary);
	}
	h3 {
		margin-top: 20px;
	}

	.point-head {
		background: #eee;
		padding: 16px;
		border-bottom: 1px solid #fff;
		border-top-left-radius: calc(var(--b_radius) * 2);
		border-top-right-radius: calc(var(--b_radius) * 2);
	}

	.point-number span {
		float: left;
		width: 80px;
		height: 80px;
		font-size: 50px;
		line-height: 75px;
		margin-right: 14px;
		font-weight: 600;
		border-radius: 50%;
		background-color: var(--primary);
		color: #fff;
		text-align: center;
		text-shadow: 0px 0px 5px rgb(0, 0, 0, .5);
	}

	span.i_next_step1:before {
		content: "1";
	}

	span.i_next_step2:before {
		content: "2";
	}

	span.i_next_step3:before {
		content: "3";
	}

	.point-body {
		padding: 16px;
	}

	.gateways {
		margin-top: 10px;

		img {
			border: 1px solid #ccc;
			margin-right: 10px;
			margin-bottom: 10px;
		}
	}

	.qr_code {
		max-width: 180px;
	}
}

// cross-sell / remap

.x-sell {
	padding: 24px;
	margin: 48px 0;
	border: 1px solid var(--primary);
	border-radius: calc(var(--b_radius) * 2);
	background-color: var(--primary_bg);

	h2 {
		color: var(--primary);
	}

	button {
		margin-bottom: 16px;
	}
}
