// DOCUMENT DOWNLOAD

.docs_download {

	button {
		width: 100%;
		border-bottom: 1px dotted var(--primary);
		padding: 0 8px;
		border-radius: 0;

		&:not(:last-child) {
			margin-bottom: 4px;
		}

		&.i_anchor {
			white-space: initial;
		}
	}

	.spinner {
		margin-left: 5px;
	}

	.docs_static {
		margin-bottom: 16px;
		margin-top: 24px;
	}

	p {
		margin-bottom: 8px;

		@include media-breakpoint-down(md) {
			margin-top: 12px;
		}
	}
}
