// default progress bar
.step_progress {
	display: flex;
	align-items: center;
	padding: 48px;

	* {
		transition: 1s;
	}

	& > * {
		flex-grow: 1;
		text-align: center;
	}

	.step_info {
		max-width: 44px; // 40px circle + 2px right/left margin
		white-space: nowrap;
		position: relative;

		.step_title,
		.step_tabs {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
		}

		.step_title {
			top: -36px; // half-circle 20px + 16px margin
			font-weight: 500;
			color: #b7b7b7;
			display: none;

			@include media-breakpoint-up(md) {
				display: initial;
			}
		}

		.step_tabs {
			bottom: -30px;

			@include media-breakpoint-down(md) {
				bottom: -36px;
			}

			span {
				display: inline-block;
				background-color: #eee;
				margin: 0 2px;
				border-radius: 50%;
				width: 8px;
				height: 8px;

				&.allow_tab_switch:hover {
					width: 10px;
					height: 10px;
					background-color: var(--text);
					border-color: var(--text);
					cursor: pointer;
				}

				@include media-breakpoint-down(md) {
					width: 14px;
					height: 14px;
				}

				&.completed {
					background-color: var(--primary);
				}

				&.current {
					background-color: #fff;
					border: 2px solid var(--primary);
				}
			}
		}

		.step_number {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 40px;
			width: 40px;
			margin-left: 2px;
			margin-right: 2px;
			border-radius: 50%;
			font-size: 20px;
			font-weight: 500;
			border: 2px solid #eee;
			background-color: #eee;
			color: #b7b7b7;
		}

		&.completed {
			.step_title {
					color: var(--primary);
				}

			.step_number {
				background-color: var(--primary);
				border-color: var(--primary);
				color: #fff;
				cursor: pointer;
			}
		}

		&.current {
			.step_title,
			.step_tabs {
				display: initial;
			}

			.step_title {
				color: var(--primary);
			}

			.step_number {
				background-color: #fff;
				border-color: var(--primary);
				color: var(--primary);
			}
		}

	}

	.step_line {
		height: 4px;
		background-color: #eee;

		&.completed {
			background-color: var(--primary);
		}

		&.pusher {
			flex-grow: 3;
		}
	}
}

// alternative progress bar (zeteo style)

.step_progress_alt {
	padding: 48px 16px;
}

.step_progress_alt_steps {
	display: flex;
	align-items: center;

	// & > * {
	// 	flex-grow: 1;
	// }

	.step_info {
		display: flex;
		position: relative;
		white-space: nowrap;
		align-items: center;
		color: #666;

		@include media-breakpoint-up(lg) {
			margin-right: 16px;
		}

		.step_number {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 32px;
			width: 32px;
			margin-right: 8px;
			border-radius: 50%;
			background-color: #fff;
			//box-shadow: -1px 7px 19px 0px rgba(133, 161, 194, 0.14);
			box-shadow: 0px 4px 8px 0px rgba(133, 161, 194, 0.15);
			border-width: 1px;
			border-style: solid;
			border-color: transparent;
		}

		.step_title {
			display: flex;

			@include media-breakpoint-down(lg) {
				display: none;
			}
		}

		&.completed {
			.step_number {
				background-color: #fff;
				color: var(--primary);
				border-color: var(--primary);

			}

			.step_title {
				color: var(--primary);
			}
		}

		&.current {

			@media (min-width: 610.1px) {
				margin-right: 16px;
			}

			.step_number {
				background-color: var(--primary);
				color: #fff;
			}

			.step_title {
				color: var(--primary);
				font-weight: 600;
				display: flex;

				@media (max-width: 610px) {
					display: none;
				}
			}
		}
	}

	.step_tabs {
		position: absolute;
		bottom: -20px;
		left: 40px;

		// @include media-breakpoint-down(md) {
		// 	bottom: -36px;
		// }

		span {
			display: inline-block;
			background-color: #fff;
			margin: 0 2px;
			border-radius: 2px;
			border-width: 1px;
			border-color: #b2b2b2;
			border-style: solid;
			width: 8px;
			height: 4px;

			&.allow_tab_switch:hover {
				width: 10px;
				height: 10px;
				background-color: var(--text);
				border-color: var(--text);
				cursor: pointer;
			}

			@include media-breakpoint-down(md) {
				width: 14px;
				height: 14px;
			}

			&.completed {
				background-color: var(--primary);
				border-color: var(--primary);
			}

			&.current {
				border-color: var(--primary);
			}
		}
	}
}

.step_progress_alt_pagination {
	position: relative;
	height: 0px;
	top: -94px;

	@media (max-width: 509px) {
		top: -44px;
	}
}
