/* MODAL TRIGGER (e.g. modal for credentials scan) */

.input-group-append .input-upload-icon {
	display: block;
	width: 36px;
	height: 36px;
	// background: skin-specific
	cursor: pointer;
	border-radius: 0 var(--b_radius) var(--b_radius) 0;
}

.input-modal-trigger p.form-control {
	background-color: transparent;
}


/* DRAG & DROP IN MODAL */
.drag-n-drop {

	.dd_desktop {
		@include media-breakpoint-down(md) {
			display: none;
		}
	}

	.dd_mobile {
		@include media-breakpoint-up(md) {
			display: none;
		}
	}

	border: 1px dashed #ccc;
	border-radius: var(--b_radius);
	background: rgba(255, 255, 255, .9);
	width: 100%;
	text-align: center;
	position: relative;
	margin-bottom: 16px;

	// webcam scan button
	button.dd_camera {
		position: absolute;
		width: 50px;
		height: 36px;
		top: 4px;
		right: 4px;
		padding: 5px;
		cursor: pointer;
		z-index: 1;

		.i_camera {
			display: inline-block;
			width: 24px;
			height: 24px;
			background: url("@/assets/images/common/glyphicons-basic-12-camera-white.svg") center center no-repeat;
		}

		@include media-breakpoint-down(md) {
			display: none;
		}
	}

	// take photo
	.scan_button {
		display: block;
		position: relative;
		margin-left: auto;
		margin-right: auto;
		bottom: 54px;
	}

	.i_dd_upload,
	.i_dd_camera {
		height: 40px;
		margin-top: 16px;
	}

	.i_dd_upload {
		background: #fff url("@/assets/images/common/glyphicons-basic-545-cloud-upload-darkgrey.svg") center center no-repeat;
	}

	.i_dd_camera {
		background: #fff url("@/assets/images/common/glyphicons-basic-12-camera-grey.svg") center center no-repeat;
	}

	p {
		color: #8d8d8d;
		margin: 16px;

		&.dd_instructions {
			font-weight: 600;

			.dd_filename {
				font-weight: 400;
			}
		}
	}

	// file upload (desktop), camera on (mobile)
	.btn-grey {
		color: #8d8d8d;
		cursor: pointer;
		margin-bottom: 16px;
	}

	// upload preview
	.scan_base64 {
		width: initial;
		max-height: 70px;
		margin-top: 16px;
	}

	input[type="file"] {
		position: absolute;
		z-index: -1;
		top: 6px;
		left: 0;
		font-size: 15px;
		color: rgb(153, 153, 153);
	}

	.i_check:not(.hidden) {
		display: inline-block;
		margin-left: 6px;
		width: 20px;
		height: 16.443px;
		background: var(--primary);
-webkit-mask-image: url("@/assets/images/general/check.svg");
		mask-image: url("@/assets/images/general/check.svg");
	}

	/* scan_credentials - WebRTC Still photo capture */
	.scan_video,
	.scan_photo,
	.scan_output,
	.scan_camera,
	.contentarea_video {
		width: 550px;
		height: 412.5px;
	}

	.scan_video,
	.scan_photo {
		border: 1px solid var(--text);
		height: 412.5px;
	}

	.scan_canvas {
		display: none;
	}

	.scan_camera,
	.scan_output {
		display: inline-block;
	}
}

.scan_feedback {
	color: var(--accent);
	border: 1px solid var(--accent);
	border-radius: var(--b_radius);
	padding: 16px;
	/* force break lines */
	white-space: pre-wrap;
	margin-bottom: 0;
	margin-top: 16px;
}


// SKINS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		.input-upload-icon {
			background: url("@/assets/images/#{$skin-slug}/forms/field-upload.svg") right center no-repeat;
		}

		.i_check:not(.hidden) {
			//background: #fff url("@/assets/images/#{$skin-slug}/general/check.svg") center center no-repeat;
		}
	}
}