// MULTIPLE input

.multiple {

	// stolen from PSC
	.multiple_dropdown {
		position: absolute;
		top: 38px; // 36px input height + 2px space
		z-index: 10;
		border: 1px solid #ccc;
		background-color: #fff;
		box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
		width: 100%;
		max-height: 360px;
		overflow-y: auto;
	}

	.multiple_input {
		cursor: pointer;
		&:hover {
			border-color: var(--primary) !important; // should remove !important
		}
	}

	.multiple_dropdown span {
		display: inline-block;
		text-align: center;
		height: 40px;
		line-height: 40px;
		font-weight: 600;
		width: 33%;
		&.accept {
			width: 34%;
			border-left: 1px dotted var(--primary);
			border-bottom: 1px dotted var(--primary);
		}
		cursor: pointer;
		&:hover {
			background-color: var(--primary_bg);
		}
	}

}

// SKINS

@each $skin-slug in $skin-list {
	&.#{$skin-slug} {
		.multiple_input {
			background-image: url("@/assets/images/#{$skin-slug}/forms/field-select.svg");
			background-repeat: no-repeat;
			background-position: right;
		}
	}
}
