// TOOLTIPS

// TTIP Active elements & icons

.ttip,
.ttip:hover {
	transition: all 0.15s;
	cursor: default;
}

// input-block & btn tooltips
.input-block:not(.input-block--alt) .ttip,
.btn .ttip {
	position: absolute;
	right: 0;
	top: 50%;
	transform: translateY(-50%);
	width: 20px;
	height: 20px;
	border-radius: 50% 0 0 50%;
	text-align: center;
	font-size: 12px;
	line-height: 20px;
	padding-left: 3px;
	background-color: #ccc;
	color: #fff;

	&:hover {
		background-color: var(--primary);
		cursor: default;
	}
}

// product warning tooltip
.ttip.pr_warning {
	position: absolute;
	left: -10px;
	top: -10px;
	width: 22px;
	height: 22px;
	border: 3px solid #fff;
	border-radius: 50%;
	font-size: 14px;
	line-height: 16px;
	color: #fff;
	background-color: #ccc;

	&:hover {
		background-color: var(--primary);
		cursor: default;
	}
}

// inline, product overview tooltips "()"
.ttip-inline, // just in case, currently not being used afaik
.pr_box_overview .ttip,
.input-block.input-block--alt .ttip {
	display: inline-block;
	position: relative;
	border-bottom: 1px dotted var(--primary);
	color: var(--primary);

	&:hover {
		color: var(--secondary);
	}
}

// TTIP text/content

.ttip {
	.ttip_text {
		visibility: hidden;
		opacity: 0;
		z-index: 1;
	}

	&:hover .ttip_text {
		visibility: visible;
		opacity: 1;
	}
}

@mixin ttip_box_style {
	// background-color: var(--primary_light);
	background-color: var(--primary);
	// REMOVED OPACITY AS TEST, DELETE AFTER A WHILE, 11.07.2023
	border-radius: var(--b_radius);
	font-size: 12px;
	color: #fff;
	text-align: left;
	padding: 4px 6px;
	position: absolute;
	z-index: 1;
	text-transform: initial; // btn tooltip
	transition: visibility 0s, opacity 0.15s linear;
}

@mixin ttip_box_arrow {
	content: " ";
	position: absolute;
	border-width: 5px;
	border-style: solid;
}

// basic style
.ttip {
	.ttip_text {
		@include ttip_box_style;
	}
}

// input block & button tooltip
.input-block:not(.input-block--alt) .ttip .ttip_text,
.btn .ttip .ttip_text {
	@include ttip_box_style;
	width: 280px;
	// right: calc(100% + 7px);
	// bottom: -4px; // -30px before, ugly for single/double line content
	right: calc(100% - 20px);
	bottom: 28px;
	// MOVED UP AS A TEST, REMOVE AFTER A WHILE, 11.07.2023
	transform: none;

	&::after {
		@include ttip_box_arrow;
		margin-top: -5px;
		// bottom: 10px; // 35px before
		// left: 100%; // arrow right
		// border-color: transparent transparent transparent var(--primary_light);
		bottom: -10px; // 35px before
		left: 95%; // arrow right
		border-color: var(--primary) transparent transparent transparent;
		// MOVED UP AS A TEST, REMOVE AFTER A WHILE, 11.07.2023
	}
}

// product overview inline tooltip ()
.pr_box_overview .ttip .ttip_text,
.input-block.input-block--alt .ttip .ttip_text {
	@include ttip_box_style;
	bottom: calc(100% + 4px);
	left: 50%;
	width: 200px;
	transform: translatex(-50%);

	&::after {
		@include ttip_box_arrow;
		top: 100%; // arrow bottom
		left: 50%;
		margin-left: -5px;
		border-color: var(--primary) transparent transparent transparent;
	}
}

// product warning tooltip - default (right)
.pr_warning.ttip .ttip_text {
	@include ttip_box_style;
	width: 250px;
	top: 8px;
	transform: translateY(-50%);
	left: calc(100% + 7px);

	&::after {
		@include ttip_box_arrow;
		top: 50%;
		right: 100%; // arrow left
		margin-top: -5px;
		border-color: transparent var(--primary) transparent transparent;
	}
}

// product warning tooltip small & medium screen (top)
@media (min-width: 543px) and (max-width: 991px) {

	.pr_warning.ttip .ttip_text {
		@include ttip_box_style;
		top: auto;
		bottom: calc(100% + 7px);
		left: 50%;
		transform: translatex(-50%);

		&::after {
			@include ttip_box_arrow;
			top: 100%; // arrow bottom
			left: 50%;
			margin-top: 0;
			margin-left: -5px;
			border-color: var(--primary) transparent transparent transparent;
		}
	}

}

// product warning tooltip large screen (left)
@include media-breakpoint-up(lg) {

	.pr_warning.ttip .ttip_text {
		@include ttip_box_style;
		left: auto;
		right: calc(100% + 7px);

		&::after {
			@include ttip_box_arrow;
			//top: 50%;
			left: 100%; //arrow right
			//margin-top: -5px;
			border-color: transparent transparent transparent var(--primary);
		}
	}

}
