// SKINS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		.input-required {
			background-image: url("@/assets/images/#{$skin-slug}/forms/asterisk.svg");
		}
	}
}

// GENERAL ICONS

span.i_download_item {
	display: inline-block;
	background-color: var(--primary);
	-webkit-mask-image: url("@/assets/images/general/download.svg");
	mask-image: url("@/assets/images/general/download.svg");
	width: 14.747px;
	height: 16px;
	transition: all 0.2s;

	&:hover {
		opacity: .8;
	}
}
