

	h3.demo,
	h4.demo,
	h5.demo {
		color: lightslategray;
		padding-left: 2px;

		&::before {
			content: '\203A  ';
		}
	}

	h3.demo {
		padding-top: 26px;
	}

	h4.demo {
		padding-left: 20px;
	}

	h5.demo {
		font-size: 14px !important;
		font-weight: normal;
		padding-left: 20px;
		margin-top: 14px !important;

	}


	.demonav {
		background-color: rgba($color: whitesmoke, $alpha: 0.9);
		border: 1px dotted var(--primary);
		padding: 5px;
		position: fixed;
		right: 10px;
		top: 10px;
		z-index: 1000;

		a {
			margin: 0 10px;
		}
	}

	#almost_done,
	#getHtml * {
		color: red;
	}

