// AUTOCOMPLETE - used in psc_dataset.js for PSC dropdown w/ suggestions

.input-autocomplete {
	width: 100%;
	position: relative;
}

.input-autocomplete > ul {
	position: absolute;
	top: 38px; // 36px input height + 2px space
	z-index: 10;
	list-style-type: none;
	padding: 5px 0;
	border: 1px solid #ccc;
	background-color: #fff;
	box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
	width: 100%;
	max-height: 160px;
	overflow-y: auto;
}

.input-autocomplete > ul > li {
	padding: 3px 20px;
	cursor: pointer;

	&:hover,
	&.active_autocomplete {
		background-color: #f5f5f5;
	}

}
