// PRODUCT

.product {
	border-radius: var(--b_radius);
	min-height: 200px;
	margin-bottom: 24px;
}

.pr_ribbon {
	position: absolute;
	left: -1px;
	top: -1px;
	z-index: 1;
	overflow: hidden;
	width: 75px;
	height: 75px;
	text-align: right;

	span {
		font-size: 10px;
		font-weight: 600;
		color: #fff;
		text-transform: uppercase;
		text-align: center;
		line-height: 20px;
		transform: rotate(-45deg);
		-webkit-transform: rotate(-45deg);
		width: 80px;
		display: block;
		background: linear-gradient(var(--primary) 0%, var(--secondary) 100%);
		box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
		position: absolute;
		top: 11px;
		left: -18px;
	}
}

.pr_bundle {
	position: relative;
	padding: 5px 10px;
	margin: 10px 0 0 0;
	background: var(--primary);
	color: #fff;
	border-radius: 4px;

	p {
		display: inline;
		margin-right: 10px;
		margin-bottom: 0;
	}

	span.bundle_cta {

		a {
			color:#fff;
			text-decoration: underline;
			white-space: nowrap;
			transition: all 0.2s;

			&:hover {
				opacity: .9;
			}
		}
	}
}

.pr_area_info,
.pr_area_price {
	padding: 4px;
}

.pr_divider {
	margin: 8px 0;
	background: var(--primary_bgdark);
	opacity: 1;
	height: 1px;
}

.pr_area_info {
	border: 1px solid var(--primary_bgdark);
	border-bottom-width: 0;
	background-color: var(--pr_bg);
	border-radius: var(--b_radius) var(--b_radius) 0 0;
	@include media-breakpoint-up(lg) {
		border-right-width: 0;
		border-bottom-width: 1px;
		border-radius: var(--b_radius) 0 0 var(--b_radius);
	}

	.pr_box_logo {
		border: 1px solid var(--primary_bgdark);
		background: #fff;
		padding: 5px;

		img {
			max-width: 100%;
			max-height: 100%;
		}
	}

	.pr_box_name {
		padding-left: 10px;
		padding-right: 10px;

		h4,
		.pr_rating,
		.pr_select {
			margin-bottom: 4px;
		}

		h4 {
			font-size: 18px;
		}

		.pr_rating {
			white-space: nowrap;
			line-height: 1;

			.i_star,
			.i_star_empty {
				background-color: var(--accent_alt);
				height: 14px;
				margin-right: 3px;
			}

			.i_star {
				width: 15.096px;
		-webkit-mask-image: url("@/assets/images/results/star.svg");
				mask-image: url("@/assets/images/results/star.svg");
			}

			.i_star_empty {
				width: 13.63px;
		-webkit-mask-image: url("@/assets/images/results/star-empty.svg");
				mask-image: url("@/assets/images/results/star-empty.svg");
			}
		}

		.pr_detail button {
			font-size: 15px;
			text-transform: uppercase;
			font-weight: 600;

			.i_detail {
				background-color: var(--primary);
		-webkit-mask-image: url("@/assets/images/results/ic_format_list_bulleted_np.svg");
				mask-image: url("@/assets/images/results/ic_format_list_bulleted_np.svg");
				width: 18.5px;
				height: 13px;
				margin-right: 4px;
			}
		}

		.pr_select p {
			margin-bottom: 0;
		}
	}

	.pr_box_overview ul,
	.pr_box_included ul,
	.pr_box_options ul {
		list-style: none;
		margin-bottom: 0;

		li {
			width: 100%;
			display: inline-block;
			@media (min-width: 543px) {
				width: 50%;
			}

			&.full {
				width: 100%;
			}
			&.incl_hide {
				display: none;
			}
		}
	}

	.pr_box_overview ul li {
		margin: 2px 0;
		padding: 3px 4px 3px 31px; // pl icon 26px + 5px gap

		&.full {
			@include media-breakpoint-up(md) {
				width: 50%;
			}
			@include media-breakpoint-up(md) {
				width: 100%;
			}
		}
	}

	.pr_box_included ul li {
		margin: 2px 0;
		height: 20px; // total height 22px, v-aligned w/ pr_box_options inputs
		padding: 0;
		padding-left: 23px; // pl icon 18px + 5px gap

			@media (min-width: 543px) {
				padding-right: 4px;
			}
	}

	.pr_box_options ul li {
		margin: 1px 0;
		padding: 0;

		&:nth-child(odd) {
			@media (min-width: 543px) {
				padding-right: 4px;
			}
		}
	}
}

.pr_area_price {
	position: relative;
	border: 1px solid var(--primary);
	background-color: #fff;

	// borders
	border-radius: 0 0 var(--b_radius) var(--b_radius);
	@include media-breakpoint-up(lg) {
		border-radius: 0 var(--b_radius) var(--b_radius) 0;
	}

	// height
	min-height: 180px;
	@media (min-width: 543px) and (max-width: 991px) {
		min-height: 120px;
	}

	.pr_discount {
		position: absolute;
		top: 10px;
		left: 10px;
		right: 10px;

		.before_discount {
				color: #666;
				text-decoration: line-through;
				position: absolute;
				left: 0;
			}

		.discount_tag {
			position: absolute;
			right: 0;
			display: inline-block;
			height: 22px;
			padding: 0 10px;
			background: #eee;
			border-radius: 0 var(--b_radius) var(--b_radius) 0;
			font-size: 12px;
			line-height: 22px;
			color: var(--text);

			&::before {
				position: absolute;
				content: "\25CF";
				color: white;
				text-shadow: 0 0 1px #333;
				font-size: 11px;
				line-height: 0px;
				text-indent: 12px;
				left: -14px;
				width: 1px;
				height: 0px;
				border-right: 14px solid #eee;
				border-top: 11px solid transparent;
				border-bottom: 11px solid transparent;
			}
		}
	}

	.pr_price {
		color: var(--primary);
		position: absolute;
		top: calc(50% - 22px);
		left: 50%;
		transform: translate(-50%,-50%);

		@media (min-width: 543px) and (max-width: 991px) {
			left: 10px;
			right: initial;
			top: 50%;
			transform: translateY(-50%);
		}

		.amount {
			font-size: 40px;
			margin-right: 4px;
		}

		.period {
			display: block;
		}
	}

	.pr_cta {
		position: absolute;
		bottom: 10px;
		left: 10px;
		right: 10px;

		@media (min-width: 543px) and (max-width: 991px) {
			right: 10px;
			left: initial;
			top: 50%;
			transform: translateY(-50%);
		}

		.btn-accent {
			background-color: var(--pr_cta);

			&:hover {
				background-color: var(--pr_cta_light);
			}

		}

		.btn-inactive {
			padding: 14px 22px 14px 12px;
			font-size: 14px;
		}
	}

	.pr_discount,
	.pr_price,
	.pr_cta {
		@media (min-width: 543px) and (max-width: 991px) {
			width: calc(50% - 15px); // 2 parts: 10px left, 10px right, 10px between = 30px/2
		}
	}
}


// PRODUCT FORM FIELDS

.pr_box_name {

	select {
		height: 22px;
		line-height: 1;
		padding: 0 26px 0 4px; //pr icon 21px + 4px gap
		padding-left: 0;
		border-color: transparent;
		width: 190px;
		display: inline-block;

		&:focus {
			border-color: transparent;
		}
	}
}

.pr_box_options {

	input[type="text"] {
		height: 22px;
		line-height: 1;
		padding: 0 4px;
	}

	select {
		height: 22px;
		line-height: 1;
		padding: 0 26px 0 4px; //pr icon 21px + 4px gap
	}

	.form-check {
		padding-top: 4px; // height 22px
		padding-bottom: 4px; // height 22px
		min-height: auto;

		input {
			width: 14px;
			height: 14px;
			border-radius: 0;
		}

		label {
			padding: 0;
			line-height: 1;
		}
	}

	.input-datepicker {
		height: 22px;
	}

	.multiple_input {
		height: 22px;
		padding-top: 0px;
		padding-bottom: 0px;
	}

	.multiple_dropdown {
		top: 23px;
		span {
			height: 30px;
			line-height: 30px;
		}
	}

}
