// BORISOV LEPSI FORMAT
/* open-sans-400 - latin_latin-ext */
@font-face {
	font-display: swap;
	font-family: 'Open Sans';
	font-style: normal;
	font-weight: 400;
	src: url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-regular.eot'); /* IE9 Compat Modes */
	src: url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
		url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-regular.woff2') format('woff2'), /* Super Modern Browsers */
		url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-regular.woff') format('woff'), /* Modern Browsers */
		url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-regular.ttf') format('truetype'), /* Safari, Android, iOS */
		url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-regular.svg#OpenSans') format('svg'); /* Legacy iOS */
}

/* open-sans-500 - latin_latin-ext */
