		url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-500.ttf') format('truetype'), /* Safari, Android, iOS */
		url('@/assets/fonts_2/open-sans-v34-latin_latin-ext-500.svg#OpenSans') format('svg'); /* Legacy iOS */
}

