// MODAL

.vue_modal {

	// Modal background
	display: block;
	position: fixed;
	z-index: 100;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgba(0, 0, 0, 0.4);
	padding: 10px;

	// Modal Content (Modal)

	.modal-content {
		margin: 100px auto;
		width: auto;
		background-color: #fff !important;
		border-radius: var(--b_radius);
		overflow-wrap: break-word;
		max-width: 600px;
	}

	.modal-header, .modal-body {
		padding: 24px;
	}

	&.modal-text .modal-header {
		padding: 24px 24px 24px 78px; // long text icon overlap fix
	}

	// marketing modals temporary heading fix (heading+content inside modal-body)
	&.modal-text .modal-header {
		padding: 24px;
		min-height: 84px;
	}

	&.modal-text .modal-body {
		padding-top: 0;
		margin-top: -60px;

		h2 {
			color: var(--primary);
			font-size: 26px;
			padding-left: 53px;
			margin-bottom: 47px;
		}
	}
	// marketing modals temporary heading fix END


	.modal-header {
		border-radius: var(--b_radius) var(--b_radius) 0 0;
		border-bottom: 1px solid #ddd;

		h4 {
			color: var(--primary);
			font-size: 26px;
			margin: 0 auto;
		}

		.i_close {
			width: 14px;
			height: 14px;
			position: absolute;
			top: 14px;
			right: 14px;
			background: url(@/assets/images/common/ic_clear_dark_np.svg) center center no-repeat;
			opacity: 0.4;
			cursor: pointer;
			transition: all 0.2s;

			&:hover,
			&:focus {
				opacity: 1;
			}
		}
	}

	.modal-body {

		ul:not(.nav) { // product detail tabs excluded
			list-style-position: outside;
			padding-left: 24px;
			margin-bottom: 24px;

				li {
					margin-bottom: 8px;
				}
		}

		p:last-child {
			margin-bottom: 0;
		}
	}

	// huge modal for product detail
	&.huge {

		.modal-header {
			background-color: var(--primary_bg);
			border-bottom: 0;
		}

		.modal-header h4 {
			color: var(--text);
			margin: 0;
		}

		.modal-content {
			margin: 40px auto;
			width: 92%;
			max-width: 1300px;
		}

		.modal-body {

			ul:not(.nav) {
				padding-left: 14px;
				margin-bottom: 14px;
			}
		}
	}
}

// MODAL HEADER ICONS, MODAL SKINS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		.modal-email .modal-header {
			background: url(@/assets/images/#{$skin-slug}/modals/ic_drafts_np.svg) 24px center no-repeat;
		}

		.modal-error .modal-header {
			background: url(@/assets/images/#{$skin-slug}/modals/ic_warning_error_np.svg) 24px center no-repeat;
		}

		.modal-credentials_scan .modal-header {
			background: url(@/assets/images/#{$skin-slug}/modals/ic_person_np.svg) 24px center no-repeat;
		}

		.modal-cleaning_session .modal-header {
			background: url(@/assets/images/#{$skin-slug}/modals/ic_cached_np.svg) 24px center no-repeat;
		}

		.modal-signature .modal-header {
			background: url(@/assets/images/#{$skin-slug}/modals/ic_border_modified_np.svg) 24px center no-repeat;
		}

		.modal-text .modal-header {
			background: var(--primary_bg) url(@/assets/images/#{$skin-slug}/modals/ic_format_np.svg) 24px center no-repeat;
		}
	}
}
