// FORM-CONTROLs - Textual form controls like <input>s, <select>s, and <textarea>s

.form-control {
	border: 1px solid #ccc;
	color: var(--text);
	background-color: #fff;
	border-radius: var(--b_radius);
	appearance: none;
	box-shadow: none;
	background-clip: border-box;
	height: 36px;
	padding: 8px;
	font-size: 1em;
}

textarea.form-control {
	height: initial;
}

.form-control[readonly]:focus {
	border: 1px solid #ccc;
}

.form-control:focus {
	border-color: var(--primary);
	box-shadow: inset 0 0 0 rgba(0, 0, 0, 0);
	background-color: #fff; // bootstrap 5.2 used to solve this, but 5.3 seems like it has to be added manually
}

.form-control::placeholder {
	color: #6c757d; // bootstrap 5.2 used to solve this, but 5.3 seems like it has to be added manually
}

input:focus,
select:focus,
input[type="text"]:focus {
	border: 1px solid var(--primary);
}

.form-control[readonly],
.form-control:disabled,
input:disabled {
	background-color: transparent;
}

input.form-control-plaintext {
	border: 1px solid transparent;
	color: var(--text);
}

input.form-control-plaintext:focus {
	border: 1px solid transparent;
}


// INPUT GROUPS

.input-group-text {
	border: 1px solid #ccc;
	border-radius: var(--b_radius);
	background: transparent;
}
