// PRODUCT DETAIL (MODAL)

.vue_modal.huge {

	.docs_download {
		background: #f7f7f7;
		padding: 14px;
		border-radius: 0 0 var(--b_radius) var(--b_radius);
	}

	// tabs

	// OLD HTML COMPATIBILITY (TO BE REMOVED/CHANGED)
	ul.nav {
		border-bottom-color: transparent // #ddd;

	}

	.nav > li > a {
		padding: 8px 16px;

		&:hover {
			background-color: #f7f7f7;
			color: var(--primary);
		}
	}

	.nav > li > a {
		border-width: 1px;
		border-style: solid;
		border-color: transparent transparent #ddd transparent;
	}

	.nav > li.active > a {
		border-color: #ddd #ddd #fff #ddd;
		border-radius: var(--b_radius) var(--b_radius) 0 0;
		color: var(--text);

		&:hover {
			background-color: transparent;
			cursor: default;
		}
	}
	// OLD HTML COMPATIBILITY END

	// tab content

	.tab-content {
		padding: 24px 0;

		h4 {
			font-size: 18px;
			letter-spacing: 1px;
			word-spacing: 2px;
			background-color: var(--primary_bg);
			padding: 8px;
			margin-bottom: 14px;
			margin-top: 24px; // toto vyhodit, ked nove html
			text-align: center;
			font-weight: 600;

			&:not(:first-of-type) {
				margin-top: 24px; // priprava na nove html
			}
		}


		h5 {
			font-size: 16px;
			line-height: 1.25;
			margin: 14px 0;
			font-weight: 600;

			&.parametre {
				padding: 3px 0 3px 35px;
				color: var(--primary);
				font-weight: 500;
			}
		}

		tr.bgcolor {
			background-color: var(--primary_bg);
		}
	}
}


// OLD HTML COMPTABILITY (TO BE REMOVED)
@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		.zahrna {
			background: url("@/assets/images/#{$skin-slug}/results/included-yes.svg") left 2px no-repeat;
		}

		.nezahrna {
			background: url("@/assets/images/#{$skin-slug}/results/included-no.svg") left 2px no-repeat;
		}

		.special {
			background: url("@/assets/images/#{$skin-slug}/results/included-special.svg") left 2px no-repeat;
		}
	}
}

.vue_modal.huge {
	ul.zahrna-nezahrna {
		list-style: none;
		text-align: left;

		> li {
			padding-left: 26px;
		}

		h5 {
			margin: 14px 0 6px;
		}
	}
}
// OLD HTML COMPTABILITY END