// TITLE AREA

span[class^="i_"] {
	display: inline-block;
}


.results_deliver {
	padding-top: 8px;
	padding-bottom: 8px;

	button:not(:last-of-type) {
		margin-right: 16px;
	}

	// span {
	// 	display: inline-block;
	// 	vertical-align: bottom;
	// 	width: 20px;
	// 	height: 20px;
	// }

	span[class^="i_"] {
		background-color: var(--primary);
		margin-right: 4px;

		&.i_email {
			mask-image: url("@/assets/images/results/envelope.svg");
			width: 16.009px;
			height: 12px;
		}

		&.i_download {
			vertical-align: sub;
			mask-image: url("@/assets/images/results/download-square.svg");
			width: 14px;
			height: 16px;
		}
	}
}



// FORM SUMMARY & GLOBAL FORM

.results_action_area {
	margin-bottom: 40px;

	.form_summary {
		background: #f7f7f7;
		margin-bottom: 3px;

		.i_anchor {
			padding-left: 5px;
		}

		.i_anchor span.i_edit {
			background-color: var(--primary);
			mask-image: url("@/assets/images/results/edit.svg");
			width: 15.768px;
			height: 14.2px;
			margin-right: 4px;
			vertical-align: middle;
		}

		.data_summary {
			padding: 8px;
			border-radius: var(--b_radius) 0 0 var(--b_radius);
		}

		.data_edit {
			background: #eee;
			padding: 8px;
			border-radius: 0 var(--b_radius) var(--b_radius) 0;
		}
	}

	.global_form {
		background: #f7f7f7;
		padding: 8px;
		border-radius: var(--b_radius);

		div[class*="col-"]:not(:last-of-type) {
			margin-bottom: 8px;
		}

		@include media-breakpoint-up(sm) {
			div.col-sm-4:not(:nth-child(3n)) {
					padding-right: 8px;
				}
		}

		label {
			font-weight: 600;
			margin-bottom: 4px;
		}

	}
}
