<template>
	<!-- everything is one button -->
	<button
		v-if="display_document"
		data-cy="document_download"
		@click="download_pdf"
		class="d-flex btn-text i_anchor"
		:class="still_waiting ? 'document_waiting' : ''"
	>

		<!-- filename, left -->
		<div class="w-100 btn-text text-start">
			{{ name }}
			<!-- loading -->
			<Bouncer_loading v-show="still_waiting" size="small" />
		</div>

		<!-- icon, right -->
		<div class="flex-shrink my-auto">
			<span class="i_download_item"></span>
		</div>

	</button>
</template>

<script>
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import Functions from '@/logic/Functions.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
			doc: Object,
		},
		components: {
			Bouncer_loading,
		},
		data() {
			return {
				is_downloading: false,
			}
		},
		created() {
		},
		methods: {
			download_pdf() {

				// exit if doc is already downloading, or not yet prepared
				if (this.still_waiting) {
					return;
				}

				// loading
				this.is_downloading = true;

				// static doc api
				if (this.is_static) {
					Get_static_docs.get_static_document(this.doc.id).then(
						(response) => {

							// transform base64 into .pdf
							Functions.downloadBase64(this.name, response.result.content);

							// loading
							this.is_downloading = false;

						}
					);
				}

				// session doc api
				else {
					Get_session_docs.get_session_document(this.doc.id).then(
						(response) => {

							// transform base64 into .pdf
							Functions.downloadBase64(this.name, response.result.content);

							// loading
							this.is_downloading = false;

						}
					);
				}

			},
		},
		computed: {
			is_static() {
				return (this.doc.filename === undefined) ? false : true;
			},
			name() {
				return this.is_static ? this.doc.filename : this.doc.name;
			},
			display_document()
			{
				return this.name !== 'ws-preview.tmp' && this.doc.documentState !== 'error';
			},
			still_waiting() {

				// loading
				if (this.is_downloading) {
					return true;
				}

				// always ready
				if (this.is_static) {
					return false;
				}

				// waiting | available | error
				if (this.doc.documentState === 'waiting') {
					return true;
				} else if (this.doc.documentState === 'available') {
					return false;
				}

				// error is hidden completely
				return true;

			},
		},
	}
</script>

<style scoped lang="css">
	.document_waiting {
		pointer-events: none;
		cursor: wait;
		filter: grayscale(100%);
	}
</style>
