// GENERAL STYLES (basic, headings etc.)


// BASIC

* {
	font-family: 'Open Sans', sans-serif;
}

.container {
	max-width: 1170px;
}

div:not(h1, h2, h3, h4) {
	font-size: 14px;
	color:var(--text);
}

p {
	margin-bottom: 16px;
}

a {
	color: var(--primary);
	text-decoration: none;
	transition: all .2s;

	&:hover {
		color: var(--secondary);
		text-decoration:none;
	}

	&:focus {
		outline: none;
	}

	&.i_anchor {
		white-space: nowrap;

		&:hover {
				// anchors w/ icons
				color: var(--primary);
				opacity: .85;
			}
	}
}

ul,
ol {
	padding-left: 0;
	list-style: inside;
}

.table {
	color: var(--text);
}

.table-bordered > :not(caption) > * {
	border-color: #ddd;
}


// TEXT & HEADINGS

b,
strong {
	font-weight: 600;
}

.text-default {
	color: var(--text)
}

.text-primary {
	color: var(--primary) !important;
}

.text-warning {
	color: var(--warning) !important;
}

.text-accent {
	color: var(--accent) !important;
}

.text-highlight {
	background: var(--primary_light);
	color: #fff;
	padding: 2px;
}

mark {
	padding: 3px;
	background: var(--accent_bg);
}

// bs5 1rem = 16px

.h1, h1,
.h2, h2,
.h3, h3,
.h4, h4,
.h5, h5,
.h6, h6 {
	line-height: 1.4;
	font-weight: 400;
}

.h1, h1,
.h2, h2 {
	margin-bottom: 22px;
}

.h1, h1 {
	font-size: calc(21.2px + 1.5vw); // calc(1.375rem + 1.5vw);

	@include media-breakpoint-up(xl) {
		font-size: 40px; // 2.5rem
	}
}

.h2, h2 {
	font-size: calc(21.2px + 0.9vw); // calc(1.325rem + 0.9vw);

	@include media-breakpoint-up(xl) {
		font-size: 32px; // 2rem
	}
}

.h3, h3 {
	font-size: calc(21.2px + 0.6vw); // calc(1.3rem + 0.6vw);

	@include media-breakpoint-up(xl) {
		font-size: 28px; // 1.75rem
	}
}


// STRUCTURE, SECTIONS & VISIBILITY

section,
.section {
	padding: 10px;
}

.bg-grey,
.bg-gray {
	background-color: #eee !important;
}

.bg-light {
	background-color: #f7f7f7 !important;
}

.hidden {
	display: none;
}

.relative {
	position: relative;
}

// TESTING

span.i_testing {
	display: inline-block;
	width: 16.009px;
	height: 12px;
	background-color: var(--primary);
	-webkit-mask-image: url("@/assets/images/results/envelope.svg");
	mask-image: url("@/assets/images/results/envelope.svg");

}