// STEP2 (RESULTS) ICONS

@each $skin-slug in $skin-list {

	&.#{$skin-slug} {

		.incl_yes {
			background: url("@/assets/images/#{$skin-slug}/results/included-yes.svg") left 1px no-repeat;
		}

		.incl_no {
			background: url("@/assets/images/#{$skin-slug}/results/included-no.svg") left 1px no-repeat;
		}

		.incl_special {
			background: url("@/assets/images/#{$skin-slug}/results/included-special.svg") left 1px no-repeat;
		}

		.pr_box_options select,
		.pr_box_options .multiple_input {
			background: #fff url("@/assets/images/#{$skin-slug}/results/field-select-s.svg") right center no-repeat;
		}

		.pr_box_name select {
			background: transparent url("@/assets/images/#{$skin-slug}/results/field-select-subtle-s.svg") right center no-repeat;

			&:hover {
				background: transparent url("@/assets/images/#{$skin-slug}/results/field-select-subtle-s-hover.svg") right center no-repeat;
			}
		}

		.pr_box_options .form-check {
			input[type="radio"] {
				background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") 0 -1px no-repeat;

				&:checked {
					background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") -15px -1px no-repeat;
				}

				&:focus:not(:checked) {
					background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") 0 -17px no-repeat;
				}
			}

			input[type="checkbox"] {
				background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") -30px -1px no-repeat;

				&:checked {
					background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") -45px -1px no-repeat;
				}

				&:focus:not(:checked) {
					background: transparent url("@/assets/images/#{$skin-slug}/results/field-radio-check-s.svg") -30px -17px no-repeat;
				}
			}
		}

		.pr_box_options .input-datepicker {
			background: #fff url("@/assets/images/#{$skin-slug}/results/field-calendar-s.svg") right center no-repeat;
		}
	}
}
